* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
.cart {
    box-shadow: rgba(0, 0, 0, 0.2) 0px 1px 2px 0px;
    display: flex;
    flex-direction: column;
    width: auto;
    justify-content: flex-start;
}

.cart__header {
    background-color: rgb(255, 255, 255);
    flex-flow: row;
    width: 100%;
    padding: 2px 2px;
    justify-content: flex-start;
}

.cart___header__title {
    font-size: 18px;
    line-height: 56px;
    padding: 0 24px;
    font-weight: 500;
}

.cart__item {
    padding: 24px;
    display: flex;
    border-top: 1px solid #f0f0f0!important;
    background: #fff;
}

.cart__item__left {
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.cart__item__left__img {
    display: block;
    position: relative;
    height: 112px;
    width: 112px;
}

.cart__item__left__img img {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    margin: auto;
    opacity: 1;
    max-width: 100%;
    max-height: 100%;

}

.cart__item__right {
    display: flex;
    flex-flow: column;
    align-content: space-around;
    margin: 0 0 0 20px;
    
}

.cart__item__right_name {
    display: block;
    font-size: 1rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding: 1px 0;
}

.cart__item__right_price {
    display: block;
    font-weight: 500;
    color: #212121;
    font-size: 1.2rem;
    text-align: left;
    padding: 1px 0;
}

.cart__item__right__remove {
    text-align: left;
    margin-top: 10px;
    flex: 1;
    
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
}

.cart__item__right__remove_btn {
    text-transform: uppercase;
    outline: 0;
    padding: .4rem .8rem;
    background-color: #fff;
    border: 1px solid #F0F3F4;
    border-radius: 4px;
    cursor: pointer;
}

.cart__total {
    display: flex;
    justify-content: flex-start;
    border-top: 1px solid #f0f0f0;
    background: #fff;
    padding: 16px 22px;
    box-shadow: 0 -2px 10px 0 rgba(0,0,0,.1);
    align-items: center;
}
.cart__total_amount{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    padding-left: 1.2rem;
    font-weight: 500;
    color: #212121;
    font-size: 1.2rem;
}

.carcart__item__left__quantity {
    display: flex;
    justify-content: center;
    padding-top: 1rem;
}

.circle_button {
    width: 28px;
    height: 28px;
    background: linear-gradient(#fff,#f9f9f9);
    border: 1px solid #c2c2c2;
    border-radius: 50%;
    cursor: pointer;
    font-size: 1rem;
    padding-top: 1px;
    line-height: 1;
    outline: 0;
}
.carcart__item__left__quantity_input input{
    display: inline-block;
    padding: 3px 6px;
    width: calc(100% - 60px);
    height: 100%;
    width: 46px;
    height: 28px;
    border-radius: 2px;
    background-color: #fff;
    border: 1px solid #c2c2c2;
    margin: 0 5px;
}
